.LeftSiteBar {
    position: absolute;
    width: 210px;
    bottom: 0px;
    left: -210px;
    top: -2px;
    font-size: 0.9em;

}
.LeftSiteBar .texts {
    margin-top: 10px;
    font-size: 0.9em;
    text-align: left;
}


.LeftSiteBar .topPart {
    height: 90px;
    background: #394b55;
    color: #d4d8da;
    position: relative !important;
    text-align: center;
}

.LeftSiteBar .resetTopPart {
    height: 0px;
}

.LeftSiteBar .hintPanel {
    background-color: #e1e9ec;
    position: absolute;
    bottom: 0px;
    left: 0px;
    right: 0px;
    top: 90px;
    overflow: auto;
}

.LeftSiteBar .resetHintPanel {
    background-color: #e1e9ec;
    position: absolute;
    bottom: 0px;
    left: 0px;
    right: 0px;
    top: 2px;
    overflow: auto;
}






